<template>
    <div class="wrapper">
        <div class="index">
            <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
            <div class="left-menu">
                <LeftMenu />
            </div>
            <div class="top-menu">
                <TopMenu />
            </div>
            <div class="aside">
                <Aside />
            </div>
            <div class="news">
