                            </div>
                            <div class="product-in-catalog__text text">
                                <p class="text__product"> {{product.name}}</p>
                                <div class="product-in-catalog__block">
                                    <p class="text__product2" >Описание товара:</p>
                                    <p class="text__description">{{product.description}}</p>
                                </div>
                                <p  class="text__product">Цена: {{product.price}} Рублей</p>
                                <div class="product-in-catalog__b">
                                    <div class="product-in-catalog__btn btn">
                                        <span class="btn__span">Подробнее</span>
                                    </div>
                                    <div class="product-in-catalog__btn btn">
                                        <span class="btn__span">К списку товаров</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="footer">
                <Footer />
            </div>
        </div>
    </div>
</template>
